import server from '@/common/server';
import context from '@/admin-pc/utils/context';
//登录
export function login(username, password, vCode, vCodeId) {
  return server.post({
    url: context.basis_baseUrl + '/basis/admin/loginBusiness',
    params: {
      username: username,
      password: password,
      vcode: vCode,
      vcodeId: vCodeId,
    },
  });
}
