<template>
  <div class="layout content-wrap">
    <Layout>
      <Header>
        <div style="width: 100%; display: flex; align-items: center; justify-content: space-between">
          <span class="title">梅河口中心医院管理平台</span>
          <Dropdown>
            <Avatar icon="ios-person" size="large" :src="s_avatar" />
            <a href="javascript:void(0)" style="color: white">
              {{ s_userName }}
              <Icon type="md-menu" />
            </a>
            <DropdownMenu slot="list">
              <DropdownItem @click.native="updatePwd">修改密码</DropdownItem>
              <DropdownItem @click.native="logout">退出登录</DropdownItem>
            </DropdownMenu>
          </Dropdown>
        </div>
      </Header>
      <Layout class="content">
        <Sider
          class="custom-sider"
          hide-trigger
          :style="{ height: '100vh', background: $my.primaryColor, overflow: 'hidden' }"
          ref="side1"
          collapsible
          :collapsed-width="100"
          v-model="isCollapsed"
        >
          <div style="overflow-y: auto; width: calc(100% + 15px); height: 100vh">
            <Menu
              active-name="1-1"
              theme="dark"
              width="auto"
              :open-names="['1-1']"
              :accordion="true"
              :class="menuitemClasses"
            >
              <Submenu :name="item.id" v-for="item in menuList" :key="item.id">
                <template slot="title">
                  <Icon type="ios-paper" />
                  <span>{{ item.title }}</span>
                </template>
                <MenuItem :name="child.name" :key="child.name" :to="child.path" v-for="child in item.children">
                  <Icon type="ios-navigate"></Icon>
                  <Tooltip :content="child.title" placement="right-start" :disabled="child.title.length < 5" :transfer="true">
                    <span class="menu-title">{{ child.title }}</span>
                  </Tooltip>
                </MenuItem>
              </Submenu>
            </Menu>
          </div>
        </Sider>
        <Layout :style="{ padding: '0 24px 24px', overflowY: 'hidden' }">
          <div style="line-height: 50px; display: flex; align-items: center">
            <Icon
              @click.native="collapsedSider"
              :class="rotateIcon"
              :style="{ margin: '0 20px' }"
              type="md-menu"
              size="24"
            />
            <Breadcrumb>
              <BreadcrumbItem>
                <router-link to="/admin-home" style="margin-left: 20px">首页</router-link>
              </BreadcrumbItem>
              <BreadcrumbItem v-if="$router.currentRoute.meta.module">
                {{ $router.currentRoute.meta.module }}
              </BreadcrumbItem>
              <BreadcrumbItem v-if="$router.currentRoute.meta.title && $router.currentRoute.meta.title !== '首页'">
                {{ $router.currentRoute.meta.title }}
              </BreadcrumbItem>
            </Breadcrumb>
          </div>
          <Content :style="{ padding: '24px', minHeight: '280px', background: '#fff', overflowY: 'hidden' }">
            <router-view></router-view>
          </Content>
        </Layout>
      </Layout>
    </Layout>
    <Modal v-model="tcModal" width="360">
      <p slot="header" style="color: #f60; text-align: center">
        <Icon type="ios-information-circle"></Icon>
        <span>系统提示</span>
      </p>
      <div style="text-align: center">
        <p>确认退出登录</p>
      </div>
      <div slot="footer">
        <Button type="error" size="large" @click="ok()">确定</Button>
        <Button type="primary" size="large" @click="cancel()">取消</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { getMenuPermissionByBusinessLoginId, loginOut } from './api/index';

export default {
  name: 'index',
  data() {
    return {
      userName: this.s_userName,
      tcModal: false,
      isCollapsed: false,
      menuList: [],
    };
  },
  computed: {
    rotateIcon() {
      return ['menu-icon', this.isCollapsed ? 'rotate-icon' : ''];
    },
    menuitemClasses() {
      return ['menu-item', this.isCollapsed ? 'collapsed-menu' : ''];
    },
  },

  methods: {
    collapsedSider() {
      this.$refs.side1.toggleCollapse();
    },
    updatePwd() {
      this.$router.replace({ path: '/hospital-updtePassword' });
    },
    logout() {
      this.tcModal = true;
    },
    cancel() {
      this.tcModal = false;
      this.$Notice.info({
        title: '已取消！',
      });
    },
    /*删除编辑弹框确定按钮*/
    ok() {
      loginOut(this.s_userName).then(res => {
        if (res.code === 0) {
          localStorage.clear();
          location.reload();
        }
      });
    },
    initRouter() {
      getMenuPermissionByBusinessLoginId().then(res => {
        if (res.code === 0) {
          this.menuList = [];
          this.menuList = res.data;
          let routes = [];
          for (let i = 0; i < this.menuList.length; i++) {
            for (let j = 0; j < this.menuList[i].children.length; j++) {
              routes.push({
                path: this.menuList[i].children[j].path,
                meta: {
                  module: this.menuList[i].title,
                  title: this.menuList[i].children[j].title,
                },
                component: resolve => require(['' + this.menuList[i].children[j].url + ''], resolve),
              });
            }
          }
          this.$router.addRoutes(routes);
        }
      });
    },
  },
  mounted() {
    this.initRouter();
  },
};
</script>

