<template>
  <div class="content-wrap">
    <div class="form-wrap form-wrap-hover">
      <div style="margin-left: 30px">
        <div style="margin: 0 auto; text-align: center">
          <span style="color: white; font-size: 38px; font-weight: 500">后台管理系统</span>
        </div>
        <div style="padding-top: 40px">
          <img src="./assets/imgs/peculiarity.gif" width="420" height="160" />
        </div>
      </div>
      <div class="form">
        <i-form ref="vo" :model="Definition" :rules="ruleInline" inline>
          <div>
            <FormItem prop="account" style="width: 240px">
              <Input type="text" v-model="Definition.account" placeholder="请输入账号" clearable />
            </FormItem>
          </div>
          <div>
            <FormItem prop="password" style="width: 240px">
              <Input type="password" v-model="Definition.password" placeholder="请输入密码" clearable />
            </FormItem>
          </div>
          <div style="width: 260px">
            <FormItem prop="vCode" style="width: 130px">
              <Input type="text" v-model="Definition.vCode" placeholder="请输入验证码" @on-enter="login" clearable />
            </FormItem>
            <img slot="append" @click="nextCaptcha" :src="captchaImg" width="100" height="34" />
          </div>
          <div>
            <FormItem>
              <Button type="primary" style="width: 240px" @click="login" :loading="loginLoading">登录 </Button>
            </FormItem>
          </div>
        </i-form>
      </div>
    </div>
    <div style="position: fixed; bottom: 10px">
      <span style="color: white">Copyright © 2012-2020 YSUSOFT. | 秦皇岛燕大燕软信息系统有限公司</span>
    </div>
    <Modal v-model="tcModal" width="360">
      <p slot="header" style="color: #f60; text-align: center">
        <Icon type="ios-information-circle"></Icon>
        <span>系统提示</span>
      </p>
      <div style="text-align: center">
        <p>{{ this.content }}</p>
      </div>
      <div slot="footer">
        <Button type="primary" size="large" @click="ok()">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { login } from './api/login';
import { getUUID } from '../common/commonFunction';
import context from '@/admin-pc/utils/context';
import md5 from 'js-md5';
export default {
  name: 'login',
  data() {
    return {
      winHeight: '',
      winWidth: '',
      captchaImg: '',
      content: '',
      tcModal: false,
      loginLoading: false,
      Definition: {
        account: '',
        password: '',
        vCode: '',
        vCodeId: '',
      },
      ruleInline: {
        account: [{ required: true, message: '请填写用户名', trigger: 'blur' }],
        password: [
          { required: true, message: '请填写密码', trigger: 'blur' },
          { type: 'string', min: 6, message: '密码长度不能小于6位', trigger: 'blur' },
        ],
        vCode: [{ required: true, message: '请填写验证码', trigger: 'blur' }],
      },
    };
  },
  methods: {
    login() {
      let pwd = md5(this.Definition.password);
      this.$refs['vo'].validate(valid => {
        if (valid) {
          this.loginLoading = true;
          login(this.Definition.account, pwd, this.Definition.vCode, this.Definition.vCodeId).then(res => {
            if (res.code === 0) {
              let token = res.data.tokenHead + res.data.token;
              this.$my.vuex('s_token', token);
              this.$my.vuex('s_userName', res.data.nickName);
              this.$my.vuex('s_orgID', res.data.sourceId);
              if (res.data.avatar) {
                this.$my.vuex('s_avatar', context.uploadAction + `/` + res.data.avatar);
              }
              setTimeout(function () {
                location.reload();
              }, 2000);
            } else {
              this.tcModal = true;
              this.content = res.msg;
            }
            this.loginLoading = false;
          });
        }
      });
    },
    /*删除编辑弹框确定按钮*/
    ok() {
      this.tcModal = false;
    },
    nextCaptcha() {
      let vcodeid = getUUID();
      this.captchaImg = context.basis_baseUrl + '/basis/admin/generationVcode?vcodeid=' + vcodeid;
      this.Definition.vCodeId = vcodeid;
    },
  },
  mounted() {
    this.winHeight = window.innerHeight - 1 + 'px';
    this.winWidth = window.innerWidth - 1 + 'px';
    this.nextCaptcha();
    let me = this;
    window.onresize = function windowResize() {
      // 通过捕获系统的onresize事件触发我们需要执行的事件
      me.winHeight = window.innerHeight - 1 + 'px';
      me.winWidth = window.innerWidth - 1 + 'px';
    };
  },
  created() {
    let me = this;
    document.onkeydown = function () {
      let key = window.event.keyCode;
      if (key === 13) {
        me.login();
      }
    };
  },
};
</script>

