import axios from 'axios';
import qs from 'querystring';
import CryptoJS from 'crypto-js';
import JSEncrypt from 'jsencrypt';
// import { getUUID } from './commonFunction';
// eslint-disable-next-line
var config = {};
// eslint-disable-next-line
var s_token = '';
// eslint-disable-next-line
var projectId = '';
// eslint-disable-next-line
var portalId = '';
// eslint-disable-next-line
var baas = axios.create(config);
// eslint-disable-next-line
var fileServer = axios.create(config);

var encryptor = new JSEncrypt(); // 新建JSEncrypt对象
var basisEncryptor = new JSEncrypt();
var RSAPrivateKey = '';
var openAes = '';
// var RSAPrivateKey = '';
const request = function () {};
request.prototype = {
  init: function (vm, options) {
    RSAPrivateKey = options.RSAPrivateKey;
    encryptor.setPublicKey(vm.s_RSAPublicJavaKey); // 设置公钥
    basisEncryptor.setPublicKey(vm.s_RSAPublicJavaBasisKey);
    openAes = options.openAes;
    config = {
      timeout: 30000,
      withCredentials: true, // 访问线上api时axios发送跨域请求时需要设置这个参数
      baseURL: options.baseUrl,
    };
    s_token = options.s_token;
    projectId = options.projectId;
    portalId = options.portalId;
    baas = axios.create(config);
    fileServer = axios.create(config);

    baas.interceptors.request.use(function (config) {
      config.headers['Authorization'] = s_token;
      config.headers['projectId'] = projectId;
      config.headers['portalId'] = portalId;
      return config;
    });
    fileServer.interceptors.request.use(function (config) {
      config.headers['Authorization'] = s_token;
      config.headers['projectId'] = projectId;
      config.headers['portalId'] = portalId;
      config.headers['Content-Type'] = 'multipart/form-data';
      return config;
    });
    //添加响应拦截器
    baas.interceptors.response.use(function (response) {
      if (response && response.headers && response.headers.token) {
        //刷新token
        options.vuex('s_token', response.headers.token);
      }
      if (response && response.data && response.data.code === 401) {
        localStorage.clear();
        location.reload();
        throw new Error('logout');
      }
      if (options.openAes && response.data.aesKey) {
        let decryptor = new JSEncrypt();
        decryptor.setPrivateKey(RSAPrivateKey);
        let AESKey = decryptor.decrypt(response.data.aesKey);
        let decrypt = CryptoJS.AES.decrypt(response.data.data, CryptoJS.enc.Utf8.parse(AESKey), {
          mode: CryptoJS.mode.ECB,
          padding: CryptoJS.pad.Pkcs7,
        });
        let decString = CryptoJS.enc.Utf8.stringify(decrypt).toString();
        return JSON.parse(decString);
      } else {
        return response.data;
      }
    });
    fileServer.interceptors.response.use(function (response) {
      if (response && response.headers && response.headers.token) {
        //刷新token
        options.vuex('s_token', response.headers.token);
      }
      if (response && response.data && response.data.code === 401) {
        localStorage.clear();
        location.reload();
        throw new Error('logout');
      }
      return response;
    });
  },
};

const post = function (options) {
  let data = {};
  if (openAes) {
    let key = 'Rhv7GmbBKBClDsdV';
    // getUUID();
    let encrypted = CryptoJS.AES.encrypt(
      CryptoJS.enc.Utf8.parse(JSON.stringify(options.params)),
      CryptoJS.enc.Utf8.parse(key),
      { mode: CryptoJS.mode.ECB, padding: CryptoJS.pad.Pkcs7 },
    );
    let aesKey = '';
    if (/(http|https):\/\/([\w.]+\/?)\S*/.test(options.url)) {
      if (options.url.indexOf('platform-api') !== -1) {
        aesKey = encryptor.encrypt(key); //后端RSA公钥加密后的AES的key
      } else {
        aesKey = basisEncryptor.encrypt(key); //后端RSA公钥加密后的AES的key
      }
    } else {
      if (config.baseURL.indexOf('platform-api') !== -1) {
        aesKey = encryptor.encrypt(key); //后端RSA公钥加密后的AES的key
      } else {
        aesKey = basisEncryptor.encrypt(key); //后端RSA公钥加密后的AES的key
      }
    }
    data = {
      data: encrypted.toString(),
      aesKey: aesKey,
    };
  } else {
    data = options.params || {};
  }

  return new window.Promise(resolve => {
    baas
      .post(options.url, data)
      .then(response => {
        resolve(response);
      })
      .catch(error => {
        // eslint-disable-next-line
        console.log(error);
      });
  });
};
const put = function (options) {
  let data = {};
  if (openAes) {
    let key = 'Rhv7GmbBKBClDsdV';
    // getUUID();
    let encrypted = CryptoJS.AES.encrypt(
      CryptoJS.enc.Utf8.parse(JSON.stringify(options.params)),
      CryptoJS.enc.Utf8.parse(key),
      { mode: CryptoJS.mode.ECB, padding: CryptoJS.pad.Pkcs7 },
    );
    let aesKey = '';
    if (/(http|https):\/\/([\w.]+\/?)\S*/.test(options.url)) {
      if (options.url.indexOf('platform-api') !== -1) {
        aesKey = encryptor.encrypt(key); //后端RSA公钥加密后的AES的key
      } else {
        aesKey = basisEncryptor.encrypt(key); //后端RSA公钥加密后的AES的key
      }
    } else {
      if (config.baseURL.indexOf('platform-api') !== -1) {
        aesKey = encryptor.encrypt(key); //后端RSA公钥加密后的AES的key
      } else {
        aesKey = basisEncryptor.encrypt(key); //后端RSA公钥加密后的AES的key
      }
    }
    data = {
      data: encrypted.toString(),
      aesKey: aesKey,
    };
  } else {
    data = options.params || {};
  }

  return new window.Promise(resolve => {
    baas
      .put(options.url, data)
      .then(response => {
        resolve(response);
      })
      .catch(error => {
        // eslint-disable-next-line
        console.log(error);
      });
  });
};
const del = function (options) {
  return new window.Promise(resolve => {
    baas
      .delete(options.url, { data: options.params || {} })
      .then(response => {
        resolve(response);
      })
      .catch(error => {
        // eslint-disable-next-line
        console.log(error);
      });
  });
};
const get = function (options) {
  return new window.Promise(resolve => {
    let data = options.params ? options.url + '?' + qs.stringify(options.params) : options.url;
    baas
      .get(data)
      .then(response => {
        resolve(response);
      })
      .catch(error => {
        // eslint-disable-next-line
        console.log(error);
      });
  });
};
const uploadFile = function (options) {
  return new window.Promise(resolve => {
    fileServer
      .post(options.url, options.params)
      .then(response => {
        resolve(response);
      })
      .catch(error => {
        // eslint-disable-next-line
        console.log(error);
      });
  });
};

const downloadFile = function (option) {
  let data = option.params ? option.url + '?' + qs.stringify(option.params) : option.url;
  return new window.Promise(resolve => {
    fileServer
      .get(data, {
        responseType: 'blob',
      })
      .then(response => {
        resolve(response);
        if (!response) {
          return;
        }
        let blobpart = new Blob([response.data]);
        let url = window.URL.createObjectURL(blobpart);
        let link = document.createElement('a');
        link.style.display = 'none';
        link.href = url;
        link.setAttribute(
          'download',
          decodeURIComponent(response.headers['content-disposition']).replace(new RegExp('attachment;filename='), ''),
        );
        document.body.appendChild(link);
        link.click();
        //释放URL对象所占资源
        window.URL.revokeObjectURL(url);
        //用完即删
        document.body.removeChild(link);
      })
      .catch(error => {
        // eslint-disable-next-line
        console.log(error);
      });
  });
};

const server = {};
server.install = function (Vue) {
  // 4. 添加实例方法
  Vue.prototype.$request = request;
  Vue.prototype.$post = post;
  Vue.prototype.$put = put;
  Vue.prototype.$get = get;
  Vue.prototype.$del = del;
  Vue.prototype.$uploadFile = uploadFile;
  Vue.prototype.$downloadFile = downloadFile;
};
export default { server, request, post, put, get, del, uploadFile, downloadFile };
