import server from '@/common/server';
import context from '@/admin-pc/utils/context';
export function getMenuPermissionByBusinessLoginId() {
  return server.get({
    url: context.basis_baseUrl + '/base/menu/getMenuPermissionByBusinessLoginId',
  });
}
//后台登出
export function loginOut(username) {
  return server.get({
    url: context.basis_baseUrl + '/basis/admin/loginOutBusiness',
    params: {
      username: username,
    },
  });
}
