import { mapState } from 'vuex';
import store from './store.js';

// 尝试将用户在根目录中的store/index.js的vuex的state变量，全部加载到全局变量中
let $storeKey = [];
try {
  $storeKey = store.myStores.state ? Object.keys(store.myStores.state) : [];
} catch (e) {
  console.log(e);
}

export default {
  created() {
    // 将vuex方法挂在到$my中
    // 使用方法为：如果要修改vuex的state中的user.name变量为"史诗" => this.$my.vuex('user.name', '史诗')
    // 如果要修改vuex的state的version变量为1.0.1 => this.$my.vuex('version', '1.0.1')
    this.$my.vuex = (name, value) => {
      this.$store.commit('$myStore', {
        name,
        value,
      });
    };
  },
  computed: {
    // 将vuex的state中的所有变量，解构到全局中
    ...mapState($storeKey),
  },
};
