<template>
  <div id="app">
    <Index v-if="is_Login" />
    <Login v-else />
  </div>
</template>

<script>
import Index from './index.vue';
import Login from './login.vue';
import server from '@/common/server';
import store from '../store/store';
import context from './utils/context';

export default {
  name: 'App',
  components: {
    Index,
    Login,
  },
  computed: {
    s_token1() {
      return this.s_token;
    },
  },
  watch: {
    s_token1: {
      immediate: true, // 这句重要
      handler(val) {
        this.$my.s_token = val;
        server.request.prototype.init(this, this.$my);
      },
    },
  },
  methods: {
    getJavaRSAPublicKey() {
      server.get({ url: '/api/1.0/security/genKeyPair' }).then(res => {
        this.$my.vuex('s_RSAPublicJavaKey', res.data);
        server.request.prototype.init(this, this.$my);
      });
    },
    getJavaBasisRSAPublicKey() {
      server.get({ url: context.basis_baseUrl + '/api/1.0/security/genKeyPair' }).then(res => {
        this.$my.vuex('s_RSAPublicJavaBasisKey', res.data);
        server.request.prototype.init(this, this.$my);
      });
    },
  },
  data() {
    return {
      is_Login: this.s_token,
    };
  },
  created() {
    store.initStore.prototype.init({ prefix: context.systemName });
    this.is_Login = this.s_token;
    //获取加密密钥
    if (context.openAes && !this.s_RSAPublicJavaKey) {
      this.getJavaRSAPublicKey();
    }
    if (context.openAes && !this.s_RSAPublicJavaBasisKey) {
      this.getJavaBasisRSAPublicKey();
    }
  },
};
</script>

<style>
@import '/assets/less/common.css';
</style>
