import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
let lifeData = {};
// 需要永久存储，且下次APP启动需要取出的，在state中的变量名
let saveStateKeys = [
  's_token',
  's_userName',
  's_staffSourceCode',
  's_projectId',
  's_avatar',
  's_userSig',
  's_userID',
  's_doctorObj',
  's_orgID',
];
let prefix = '';
const initStore = function () {};
const myStores = new Vuex.Store({
  state: {
    // 如果上面从本地获取的lifeData对象下有对应的属性，就赋值给state中对应的变量
    s_token: lifeData.s_token ? lifeData.s_token : '',
    // 如果vuex_version无需保存到本地永久存储，无需lifeData.vuex_version方式
    s_version: '1.0.0',
    s_userName: lifeData.s_userName ? lifeData.s_userName : '',
    s_staffSourceCode: lifeData.s_staffSourceCode ? lifeData.s_staffSourceCode : '',
    s_projectId: lifeData.s_projectId ? lifeData.s_projectId : '',
    s_avatar: lifeData.s_avatar ? lifeData.s_avatar : '',
    s_messageList: [],
    s_userSig: lifeData.s_userSig ? lifeData.s_userSig : '',
    s_userID: lifeData.s_userID ? lifeData.s_userID : '',
    s_doctorObj: lifeData.s_doctorObj ? lifeData.s_doctorObj : {},
    s_orgID: lifeData.s_orgID ? lifeData.s_orgID : '',
    s_RSAPublicJavaKey: '',
    s_RSAPublicJavaBasisKey: '',
  },

  mutations: {
    $myStore(state, payload) {
      // 判断是否多层级调用，state中为对象存在的情况，诸如user.info.score = 1
      let nameArr = payload.name.split('.');
      let saveKey = '';
      let len = nameArr.length;
      if (len >= 2) {
        let obj = state[nameArr[0]];
        for (let i = 1; i < len - 1; i++) {
          obj = obj[nameArr[i]];
        }
        obj[nameArr[len - 1]] = payload.value;
        saveKey = nameArr[0];
      } else {
        // 单层级变量，在state就是一个普通变量的情况
        state[payload.name] = payload.value;
        saveKey = payload.name;
      }
      // 保存变量到本地，见顶部函数定义
      saveLifeData(saveKey, state[saveKey]);
    },
  },
});
const commitData = function (name, value) {
  myStores.commit('$myStore', { name, value });
};
initStore.prototype = {
  init: function (options) {
    prefix = options.prefix;
    try {
      // 尝试获取本地是否存在lifeData变量，第一次启动是不存在的
      lifeData = JSON.parse(localStorage.getItem(prefix + '_' + 's_lifeData')) || {};
      commitData('s_token', lifeData.s_token ? lifeData.s_token : '');
      commitData('s_version', '1.0.0');
      commitData('s_userName', lifeData.s_userName ? lifeData.s_userName : '');
      commitData('s_projectId', lifeData.s_projectId ? lifeData.s_projectId : '');
      commitData('s_staffSourceCode', lifeData.s_staffSourceCode ? lifeData.s_staffSourceCode : '');
      commitData('s_avatar', lifeData.s_avatar ? lifeData.s_avatar : '');
      commitData('s_userSig', lifeData.s_userSig ? lifeData.s_userSig : '');
      commitData('s_userID', lifeData.s_userID ? lifeData.s_userID : '');
      commitData('s_doctorObj', lifeData.s_doctorObj ? lifeData.s_doctorObj : {});
      commitData('s_orgID', lifeData.s_orgID ? lifeData.s_orgID : '');
    } catch (e) {
      console.log(e);
    }
  },
};
// 保存变量到本地存储中
const saveLifeData = function (key, value) {
  // 判断变量名是否在需要存储的数组中
  if (saveStateKeys.indexOf(key) !== -1) {
    // 获取本地存储的lifeData对象，将变量添加到对象中
    let tmp = localStorage.getItem(prefix + '_' + 's_lifeData');
    // 第一次打开APP，不存在lifeData变量，故放一个{}空对象
    tmp = tmp ? JSON.parse(tmp) : {};
    tmp[key] = value;
    // 执行这一步后，所有需要存储的变量，都挂载在本地的lifeData对象中
    localStorage.setItem(prefix + '_' + 's_lifeData', JSON.stringify(tmp));
  }
};
