import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

const routes = [
  {
    path: 'admin-home',
    meta: {
      title: '首页',
    },
    component: resolve => require(['/'], resolve),
  },
  {
    path: '/hospital-updtePassword',
    meta: {
      title: '修改密码',
    },
    component: resolve => require(['./view/hospital-updtePassword/updtePassword.vue'], resolve),
  },
];
const router = new Router({ base: '/mhk_admin', routes }); //mode: 'history',//base: '/es',
// const router = new Router({ base: '/', routes }); //mode: 'history',//base: '/es',
router.beforeEach((to, from, next) => {
  if (to.path === '/') {
    next('/deptIncomeContrastiveAnalysis/deptIncomeContrastiveAnalysis');
  }
  const title = to.meta && to.meta.title;
  if (title) {
    document.title = title;
  }
  next();
});
export { router };
